// ====================== Reset ======================

@import "modules/reset";

// ==============  Responsive Font Size  =============

@import "../../../node_modules/rfs/sass";

// ===================  Modules  =====================

@import "modules/mixin_font-face";
@import "modules/fontstylesheet";
@import "modules/variables";

// ===================================================
//      Uncomment if png or svg folder not empty
// ===================================================
@import "modules/mixin-spritePng";
@import "tmp/spritePng";

// @import tmp/spriteSvg
// ====================  Plugins  ====================

@import "../../../node_modules/animate.css/animate";
@import "../../../node_modules/swiper/dist/css/swiper.min.css";

// ====================  Default  ====================

p {
  hyphens: auto; }

button,
a,
input {
  outline: 0; }

button,
a {
  cursor: pointer; }

a {
  text-decoration: none; }

.icon {
  transition: fill ease-in-out 0.2s; }

*,
*:before,
*:after {
  box-sizing: border-box; }

body {
  @include rfs(16);

  color: $textcolor;
  font-family: $montserrat;
  line-height: 1.6; }

html,
body {
  margin: 0;
  padding: 0;
  height: 100%; }

.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0 auto;
  padding: 0 15px;
  width: 100%;
  max-width: 1170px; }

// ===================  Partials  ===================

@import "partials/header";
@import "partials/section";
@import "partials/about";
@import "partials/responsive";
@import "partials/whoweare";
@import "partials/feauters";
@import "partials/work";
@import "partials/service";
@import "partials/pricing";
@import "partials/expert";
@import "partials/banner";
@import "partials/news";
@import "partials/contact";
@import "partials/map";
@import "partials/footer";

// ==================== media ====================

@media (max-width: 992px) {
  .about,
  .responsive,
  .whoweare,
  .features,
  .service,
  .pricing,
  .expert,
  .banner,
  .news,
  .contact {
    padding: 80px 0; }

  .work {
    padding: 80px 0 0 0; }

  .slider {
    &__desc {
      max-width: 600px; } }

  .about {
    .section {
      &__subtitle {
        top: 31%; } }

    &__title {
      @include rfs(20); }

    &__text {
      line-height: 1.2; } }

  .responsive {
    .section {
      &__title {
        margin-left: 75px; }

      &__subtitle {
        top: 33%;
        left: 51%; } } }

  .features {
    &__conent {
      justify-content: center; }

    &__item {
      margin: 0 auto;
      width: 50%;
      align-items: center; }

    &__title {
      @include rfs(16); } }

  .service {
    .section__subtitle {
      @include rfs(26); }

    &__cards {
      padding: 80px 0 0 0; } }

  .pricing {
    &__cards {
      padding: 80px 0 0 0; }

    &__list {
      padding: 30px 0 0 0; }

    &__item {
      padding: 10px 0; } }

  .expert {
    &__cards {
      padding: 80px 0 0 0; } }

  .back {
    padding: 48px 0 0 0;

    &__socials {
      padding: 22px 5px; } }

  .banner {
    &__buttons {
      padding: 80px 0 0 0; } }

  .news {
    &__cards {
      padding: 80px 0 0 0; }

    &__desc {
      max-width: 180px; } }

  .contact {
    &__form {
      padding: 80px 0 0 0; } } }

@media (max-width: 780px) {
  .about,
  .responsive,
  .whoweare,
  .features,
  .service,
  .pricing,
  .expert,
  .banner,
  .news,
  .contact {
    padding: 60px 0; }

  .work {
    padding: 60px 0 0 0; }

  .stats {
    padding: 80px 0 0 0; }

  .slider {
    &__desc {
      max-width: 500px; } }

  .nav {
    &__menu {
      position: absolute;
      top: 79px;
      left: 0;
      display: none;
      width: calc(100% + 30px);
      height: calc(100vh - 79px);
      flex-direction: column;
      align-items: center;
      background: #2a2a2a;
      margin: 0 -15px;
      transition: all .2s linear;
      &--active {
        display: flex;
        z-index: 99; } } }

  .nav-burger {
    display: block; }

  .about {
    &__card {
      height: 300px;

      &:hover .about__text {
        height: 40%; } }

    &__icon {
      transform: scale(0.8); }

    &__title {
      padding: 0 0 10px 0;

      @include rfs(20);

      line-height: 1.1; }

    &__text {
      @include rfs(14);

      max-width: 85%; }

    &__button {
      padding: 10px 35px; } }

  .responsive {
    .section {
      &__title {
        margin-left: 90px; }

      &__subtitle {
        top: 26%;
        left: 49%; } } }

  .whoweare {
    &__button {
      padding: 10px 35px; } }

  .features {
    &__content {
      padding: 60px 0 0 0; }

    &__item {
      width: 50%;
      align-items: center; }

    &__title {
      text-align: center; }

    &__text {
      text-align: justify;
      word-break: break-all; }

    &__icon {
      width: 50px;
      height: 50px; } }

  .work {
    &__card {
      width: 50%; } }

  .service {
    background: none;

    .section {
      &__header {
        align-items: center;
        text-align: center; }

      &__subtitle {
        @include rfs(18); } }

    &__content {
      width: 100%; }

    &__title {
      @include rfs(16);

      padding: 25px 0; } }

  .pricing {
    &__cards {
      padding: 60px 0 0 0;
      margin: 0 -5px; }

    &__card {
      margin: 0 5px; }

    &__price {
      padding: 20px 0 30px 0; }

    &__list {
      padding: 15px 0 0 0; }

    &__item {
      padding: 5px 0; } }

  .expert {
    &__cards {
      padding: 60px 0 0 0; } }

  .back {
    padding: 30px 0 0 0;

    &__title,
    &__text,
    &__socials {
      max-width: 90%; }

    &__name {
      @include rfs(16); }

    &__position {
      @include rfs(12); }

    &__text {
      line-height: 1.2; }

    &__socials {
      padding: 11px 0; } }

  .banner {
    &__buttons {
      padding: 60px 0 0 0; } }

  .news {
    &__cards {
      padding: 60px 0 0 0;
      flex-wrap: wrap; }

    &__card {
      width: calc(50% - 26px);
      margin: 13px; }

    &__desc {
      max-width: 100%; }

    &__title {
      padding: 18px 0 0 0; } }

  .contact {
    &__form {
      padding: 60px 0 0 0; }

    &__form div input {
      padding: 10px; }

    &__button {
      padding: 10px 35px; } }

  .footer {
    &__inner {
      padding: 60px 0; }

    &__soc-item {
      padding: 0 15px; } } }

@media (max-width: 575px) {
  .about,
  .responsive,
  .whoweare,
  .features,
  .service,
  .pricing,
  .expert,
  .banner,
  .news,
  .contact,
  .footer__inner {
    padding: 30px 0; }

  .work,
  .contact__form,
  .news__cards,
  .expert__cards {
    padding: 30px 0 0 0; }

  .about {
    .section {
      &__text {
        padding-top: 120px; } }

    &__buttons {
      padding: 40px 0 0 0; }

    &__button {
      text-align: center;
      padding: 10px 20px;

      @include rfs(16); }

    &__cards {
      flex-direction: column;
      padding: 40px 0 0 0; }

    &__card {
      margin-bottom: 15px;
      width: calc(100% - 15px);

      &:hover .about__text {
        height: 30%;

        @include rfs(16); } } }

  .stats {
    &__counters {
      padding: 30px 0 0 0;
      flex-direction: column; }

    &__item {
      padding: 15px 0; } }

  .responsive {
    .section {
      &__header {
        text-align: center; }

      &__title {
        margin-left: 0; }

      &__subtitle {
        left: 50%;
        transform: translateX(-50%); } }

    &__lamp {
      display: none; }

    &__inner {
      flex-direction: column; }

    &__text,
    &__run {
      width: 100%; } }

  .whoweare {
    &__button {
      padding: 10px 20px;

      @include rfs(16); } }

  .features {
    &__item {
      width: 100%;
      padding: 0 15px 40px; } }

  .filter {
    flex-direction: column;
    align-items: stretch;
    width: calc(100% - 15px); }

  .work {
    &__card {
      margin: 0 15px 15px 15px;
      width: calc(100% - 15px); } }

  .service {
    padding-top: 60px;

    &__cards {
      flex-direction: column;
      padding: 30px 0 0 0; }

    &__card {
      width: 100%;
      border-bottom: 1px solid #e6e6e6;

      &:first-child {
        border-right: 0; }

      &:nth-child(3) {
        border-right: 0; }

      &:last-child {
        border-bottom: 0; } } }

  .pricing {
    &__cards {
      flex-direction: column; }

    &__card {
      width: 100%;
      margin: 0 auto;
      padding: 0 0 30px 0; }

    &__list {
      display: none; } }

  .expert {
    &__cards {
      flex-direction: column;
      align-items: center; }

    &__card {
      margin-top: 30px;
      width: calc(80% - 30px); } }

  .news {
    &__cards {
      justify-content: center;
      align-items: center; }

    &__card {
      width: calc(80% - 26px); } }

  .banner {
    text-align: center;

    &__buttons {
      display: flex;
      flex-direction: column;
      padding: 30px 0 0 0; }

    &__button {
      margin-top: 15px;

      @include rfs(16); } }

  .footer {
    &__inner {
      flex-direction: column;
      justify-content: space-around;
      align-items: stretch; }

    &__socials {
      order: 1;
      padding: 15px 0; }

    &__phone,
    &__location {
      flex-direction: row;
      justify-content: space-between;
      padding: 15px 0; }

    &__phone-number,
    &__location-adress {
      padding: 0; } } }

