  padding: 10px 0;
  border: 0;
  background: none;
  color: transparent;
  font-size: 0;
  cursor: pointer;
}

.nav-burger:hover .nav-burger__line,
.nav-burger:hover .nav-burger__line::before,
.nav-burger:hover .nav-burger__line::after {
  background: #ffe400;
}

.nav-burger__line {
  position: relative;
  display: block;
  width: 30px;
  height: 3px;
  background: #fff;
  transition: all .2s linear;
}

.nav-burger__line::before, .nav-burger__line::after {
  position: absolute;
  left: 0;
  display: block;
  width: 30px;
  height: 3px;
  background: #fff;
  content: '';
  transition: all .2s linear;
}

.nav-burger__line::before {
  top: -10px;
}

.nav-burger__line::after {
  top: 10px;
}

.nav-burger--active:hover .nav-burger__line {
  background: none;
}

.nav-burger--active:hover .nav-burger__line::before,
.nav-burger--active:hover .nav-burger__line::after {
  background: #ffe400;
}

.nav-burger--active .nav-burger__line {
  background: none;
}

.nav-burger--active .nav-burger__line::before {
  transform: rotate(45deg);
  transform-origin: left top;
  transition: transform .2s linear;
}

.nav-burger--active .nav-burger__line::after {
  transform: rotate(-45deg);
  transform-origin: left bottom;
  transition: transform .2s linear;
}

