  .news,
  .contact {
    padding: 60px 0;
  }
  .work {
    padding: 60px 0 0 0;
  }
  .stats {
    padding: 80px 0 0 0;
  }
  .slider__desc {
    max-width: 500px;
  }
  .nav__menu {
    position: absolute;
    top: 79px;
    left: 0;
    display: none;
    width: calc(100% + 30px);
    height: calc(100vh - 79px);
    flex-direction: column;
    align-items: center;
    background: #2a2a2a;
    margin: 0 -15px;
    transition: all .2s linear;
  }
  .nav__menu--active {
    display: flex;
    z-index: 99;
  }
  .nav-burger {
    display: block;
  }
  .about__card {
    height: 300px;
  }
  .about__card:hover .about__text {
    height: 40%;
  }
  .about__icon {
    transform: scale(0.8);
  }
  .about__title {
    padding: 0 0 10px 0;
    font-size: 1.25rem;
    line-height: 1.1;
  }
  .about__text {
    font-size: 0.875rem;
    max-width: 85%;
  }
  .about__button {
    padding: 10px 35px;
  }
  .responsive .section__title {
    margin-left: 90px;
  }
  .responsive .section__subtitle {
    top: 26%;
    left: 49%;
  }
  .whoweare__button {
    padding: 10px 35px;
  }
  .features__content {
    padding: 60px 0 0 0;
  }
  .features__item {
    width: 50%;
    align-items: center;
  }
  .features__title {
    text-align: center;
  }
  .features__text {
    text-align: justify;
    word-break: break-all;
  }
  .features__icon {
    width: 50px;
    height: 50px;
  }
  .work__card {
    width: 50%;
  }
  .service {
    background: none;
  }
  .service .section__header {
    align-items: center;
    text-align: center;
  }
  .service .section__subtitle {
    font-size: 1.125rem;
  }
  .service__content {
    width: 100%;
  }
  .service__title {
    font-size: 1rem;
    padding: 25px 0;
  }
  .pricing__cards {
    padding: 60px 0 0 0;
    margin: 0 -5px;
  }
  .pricing__card {
    margin: 0 5px;
  }
  .pricing__price {
    padding: 20px 0 30px 0;
  }
  .pricing__list {
    padding: 15px 0 0 0;
  }
  .pricing__item {
    padding: 5px 0;
  }
  .expert__cards {
    padding: 60px 0 0 0;
  }
  .back {
    padding: 30px 0 0 0;
  }
  .back__title, .back__text, .back__socials {
    max-width: 90%;
  }
  .back__name {
    font-size: 1rem;
  }
  .back__position {
    font-size: 0.75rem;
  }
  .back__text {
    line-height: 1.2;
  }
  .back__socials {
    padding: 11px 0;
  }
  .banner__buttons {
    padding: 60px 0 0 0;
  }
  .news__cards {
    padding: 60px 0 0 0;
    flex-wrap: wrap;
  }
  .news__card {
    width: calc(50% - 26px);
    margin: 13px;
  }
  .news__desc {
    max-width: 100%;
  }
  .news__title {
    padding: 18px 0 0 0;
  }
  .contact__form {
    padding: 60px 0 0 0;
  }
  .contact__form div input {
    padding: 10px;
  }
  .contact__button {
    padding: 10px 35px;
  }
  .footer__inner {
    padding: 60px 0;
  }
  .footer__soc-item {
    padding: 0 15px;
  }
}

@media (max-width: 575px) {
  .about,
  .responsive,
  .whoweare,
  .features,
  .service,
  .pricing,
  .expert,
  .banner,
  .news,
  .contact,
  .footer__inner {
    padding: 30px 0;
  }
  .work,
  .contact__form,
  .news__cards,
  .expert__cards {
    padding: 30px 0 0 0;
  }
  .about .section__text {
    padding-top: 120px;
  }
  .about__buttons {
    padding: 40px 0 0 0;
  }
  .about__button {
    text-align: center;
    padding: 10px 20px;
    font-size: 1rem;
  }
  .about__cards {
    flex-direction: column;
    padding: 40px 0 0 0;
  }
  .about__card {
    margin-bottom: 15px;
    width: calc(100% - 15px);
  }
  .about__card:hover .about__text {
    height: 30%;
    font-size: 1rem;
  }
  .stats__counters {
    padding: 30px 0 0 0;
    flex-direction: column;
  }
  .stats__item {
    padding: 15px 0;
  }
  .responsive .section__header {
    text-align: center;
  }
  .responsive .section__title {
    margin-left: 0;
  }
  .responsive .section__subtitle {
    left: 50%;
    transform: translateX(-50%);
  }
  .responsive__lamp {
    display: none;
  }
  .responsive__inner {
    flex-direction: column;
  }
  .responsive__text, .responsive__run {
    width: 100%;
  }
  .whoweare__button {
    padding: 10px 20px;
    font-size: 1rem;
  }
  .features__item {
    width: 100%;
    padding: 0 15px 40px;
  }
  .filter {
    flex-direction: column;
    align-items: stretch;
    width: calc(100% - 15px);
  }
  .work__card {
    margin: 0 15px 15px 15px;
    width: calc(100% - 15px);
  }
  .service {
    padding-top: 60px;
  }
  .service__cards {
    flex-direction: column;
    padding: 30px 0 0 0;
  }
  .service__card {
    width: 100%;
    border-bottom: 1px solid #e6e6e6;
  }
  .service__card:first-child {
    border-right: 0;
  }
  .service__card:nth-child(3) {
    border-right: 0;
  }
  .service__card:last-child {
    border-bottom: 0;
  }
  .pricing__cards {
    flex-direction: column;
  }
  .pricing__card {
    width: 100%;
    margin: 0 auto;
    padding: 0 0 30px 0;
  }
  .pricing__list {
    display: none;
  }
  .expert__cards {
    flex-direction: column;
    align-items: center;
  }
  .expert__card {
    margin-top: 30px;
    width: calc(80% - 30px);
  }
  .news__cards {
    justify-content: center;
    align-items: center;
  }
  .news__card {
    width: calc(80% - 26px);
  }
  .banner {
    text-align: center;
  }
  .banner__buttons {
    display: flex;
    flex-direction: column;
    padding: 30px 0 0 0;
  }
  .banner__button {
    margin-top: 15px;
    font-size: 1rem;
  }
  .footer__inner {
    flex-direction: column;
    justify-content: space-around;
    align-items: stretch;
  }
  .footer__socials {
    order: 1;
    padding: 15px 0;
  }
  .footer__phone, .footer__location {
    flex-direction: row;
    justify-content: space-between;
    padding: 15px 0;
  }
  .footer__phone-number, .footer__location-adress {
    padding: 0;
  }
}
